@import url(https://fonts.googleapis.com/css?family=Cutive+Mono|Montserrat:400,700,800&display=swap);
.sprite{background-image:url(data:image/png;base64,);background-repeat:no-repeat;display:block}.sprite-CSSTricks{width:30px;height:32px;background-position:-1px -1px}.sprite-CodePen{width:32px;height:32px;background-position:-33px -1px}.sprite-GitHub{width:32px;height:32px;background-position:-1px -35px}.sprite-SkillShare{width:65px;height:32px;background-position:-67px -1px}.sprite-YouTube{width:45px;height:32px;background-position:-35px -35px}.sprite-freeCodeCamp{width:47px;height:32px;background-position:-82px -35px}html{font-family:"Cutive Mono",monospace}body{margin:0}section{padding:80px 20px;padding-right:calc(20px + 50px)}section>h1{font-size:1.7em}section>h1::after{content:"_"}section article{width:calc(100vw - 20px * 2 - 50px)}@media screen and (min-width: 650px){section{padding:80px 40px}section>h1{font-size:1.5em}section article{width:auto}}
nav{position:fixed;background-color:#d3d3d3;right:0;top:-20px;height:50px;display:flex;align-items:center;padding:0 10px;-webkit-transform:rotate(-90deg);transform:rotate(-90deg);-webkit-transform-origin:bottom right;transform-origin:bottom right;z-index:10}nav a{display:inline-block;margin:0 10px;-webkit-transform:rotate(180deg);transform:rotate(180deg);color:gray;font-size:1.1em;text-decoration:none}nav a::after{content:"_"}@media screen and (min-width: 650px){nav{right:30px;top:20px}nav a{font-size:1.3em}}
#header{font-family:"Montserrat",sans-serif}#header .hi{color:#192b3e;font-size:3em;font-weight:800;margin:.3em 0}#header .subheader{margin:4em 0 0em;max-width:700px;font-size:1.8em;font-weight:700;color:#898ea2}#header .subheader .job-title{color:#192b3e}#header .description{font-family:"Montserrat",sans-serif;max-width:700px;line-height:1.8em;margin-top:1.5em}
.preview{display:inline-block;position:relative;isolation:isolate;border:1px solid rgba(128,128,128,.4);box-sizing:border-box;font-size:0;width:100%}.preview a{display:flex;align-items:center;color:gray;text-decoration:none}.preview img{width:100%;height:auto}.preview .desc{box-sizing:border-box;font-size:16px;padding:40px}.preview .desc h1{font-size:5em;margin:0}.preview .desc h2{display:none}.preview .desc::before{content:"";float:left;padding-top:100%}.preview .run{position:absolute;width:100%;height:100%;top:0;left:0;display:flex;align-items:center;justify-content:center;font-size:128px;-webkit-user-select:none;user-select:none;cursor:pointer;background-color:#fff;mix-blend-mode:luminosity;opacity:0}.preview .run:hover{opacity:.5}@media screen and (min-width: 650px){.preview{width:auto}.preview img,.preview .desc{width:500px;height:500px}.preview .desc h2{display:block}}
.source{margin:20px 0}.source a{text-decoration:none;color:#000}.source .sprite{display:inline-block;vertical-align:middle;margin-right:10px}
.techs li::before{content:"#";color:gray}.techs ul{list-style-type:none;margin:0;padding:0}
#hobby article{display:inline-block;margin:50px 0 50px 0;vertical-align:top}#hobby article .date{color:gray}#hobby article .date::before{content:"// "}#hobby article .info{display:inline-block;vertical-align:bottom;width:180px}@media screen and (min-width: 650px){#hobby article{margin:0}#hobby article h1{max-width:500px}#hobby article h1 span{display:inline-block}#hobby article .info{padding-left:20px}}
#skills{margin:40px}#skills .skillSection{display:inline-block;vertical-align:top;margin:20px 80px 20px 0}.skill{padding-bottom:10px;cursor:pointer;white-space:pre}.skillName{display:inline-block;width:120px;height:22px;text-align:right;margin-right:10px}.selectedSkill{background-color:#ff0}.skillScore{font-size:1.4em;vertical-align:middle}.skillScore .skillDeficit{color:#d3d3d3}
.hobby-items{display:grid;grid-template-columns:repeat(1, 1fr);grid-template-rows:repeat(auto-fill, auto);grid-row-gap:130px;grid-column-gap:50px}@media screen and (min-width: 1200px){.hobby-items{grid-template-columns:repeat(2, 1fr)}}
.resume-block{display:inline-block;font-family:"Montserrat",sans-serif;margin:50px 80px 80px 0;width:100%}.resume-block h3{font-weight:normal}.resume-block .sub{font-size:.8em;color:gray}.resume-block img{width:100%;cursor:pointer}@media screen and (min-width: 650px){.resume-block{width:300px}}
#resume article{display:inline-block;vertical-align:top;margin:40px 0 50px 0}#resume article h2{text-transform:uppercase}@media screen and (min-width: 650px){#resume article{margin-right:60px}}
#contact{background-color:#f8f7f2}#contact .icon{font-size:1.5em}#contact a{display:block;line-height:1.5em}#contact .column{display:inline-block;vertical-align:top;margin:20px 80px 20px 0}#contact>div{margin-bottom:15px}#contact>div:first-of-type a{background-color:#90ee90;line-height:2em;padding:25px 25px}
